.menu {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    border: 1px solid;
    list-style: none;
}

.link > a {
    text-decoration: none;
    border: 1px solid;
    color: black;
    font-size: 20px;
}

.link > a:hover {
    background-color: green;
}